.team-info {
  display: flex;
  align-items: center;
  justify-content: center;

  .team-bg-box {
    height: 1965px;
    width: 100%;
    background: linear-gradient(99.25deg, rgba(55, 86, 205, .2) 23.49%, rgba(205, 122, 55, .2) 83.55%);
    .team-title {
      text-align: center;
      margin-top: 63px;
      color: #FFFFFF;
      font-size: 48px;
      font-style: normal;
      font-weight: normal;
    }
    .team-describe{  
      width: 1334px;
      text-align: center;
      font-size: 20px;
      margin: 0 auto;
      margin-top: 60px;

    }
  }
}